nav {
  width: 100%;
  height: 10vh;
  background-color: #4a4a4a;
  position: fixed;
  bottom: 0;
  z-index: 1; }

.Desktop {
  display: none; }

.Mobile {
  display: inline-block;
  width: 100%;
  height: 10vh;
  background-color: #4086bb;
  position: fixed;
  top: 0;
  z-index: 1;
  padding: 0 2rem; }
  .Mobile a {
    color: white;
    text-decoration: none; }
  .Mobile ul {
    list-style-type: none;
    margin: auto;
    padding: 0;
    width: 100%;
    height: 100%; }
  .Mobile li {
    display: inline-block; }
    .Mobile li a {
      display: block;
      color: white;
      text-align: center;
      padding: 14px 16px;
      text-decoration: none; }
    .Mobile li a:hover {
      text-decoration: none; }

.navbar-brand {
  display: none; }

.brand-text {
  font-family: lato;
  font-size: 2rem;
  color: white;
  font-weight: 900;
  margin-right: auto; }

@media only screen and (min-width: 600px) {
  nav {
    width: 100%;
    height: 10vh;
    background-color: #4086bb;
    position: fixed;
    top: 0;
    z-index: 1;
    padding: 0 5rem; }
    nav ul {
      list-style-type: none;
      margin: auto;
      padding: 0;
      width: 100%;
      height: 100%; }
    nav li {
      display: inline-block; }
      nav li a {
        display: block;
        color: white;
        text-align: center;
        padding: 14px 16px;
        text-decoration: none; }
      nav li a:hover {
        text-decoration: none; }
  .Desktop {
    display: inline-block; }
  .navbar-brand {
    display: block;
    font-family: lato;
    font-size: 2rem;
    color: white;
    font-weight: 900;
    margin-right: auto; }
  .Mobile {
    display: none; } }



